.demo {
  overflow: hidden;

  background-color: #fff;
  width: 100vw;
  height: 100vh;

  display: contents;

  .top-nav {
    width: 100%;
    height: 52px;
    padding: 12px;
    background-color: #ffffff;
    border-bottom: 1px solid gray;
    border-radius: 2px;

    display: flex;
    align-items: center;

    .top-nav-logo {
      cursor: pointer;
      width: 134px;
      min-width: 134px;
      margin-left: 12px;
      display: flex;
      flex-direction: column;
    }
  }

  .content {
    display: flex;

    .sider {
      height: calc(100vh - 52px);

      background-color: #fafbfc;

      border-right: 1px solid lightgray;
    }

    .sider::-webkit-scrollbar {
      width: 8px; /* width of the entire scrollbar */
      height: 8px;
      cursor: pointer;
    }
    .sider::-webkit-scrollbar-track {
      background: rgba(240 240 240 / 80%); /* color of the tracking area */
      cursor: pointer;
    }
    .sider::-webkit-scrollbar-thumb {
      background-color: rgba(
        177,
        177,
        177,
        0.4
      ); /* color of the scroll thumb */
      border-radius: 20px; /* roundness of the scroll thumb */
      border: 1px solid rgba(177, 177, 177, 0.4); /* creates padding around scroll thumb */
      cursor: pointer;
    }

    .main-content {
      width: -webkit-fill-available;
      height: calc(100vh - 52px);
      background-color: #ffffff;
      overflow: auto;
      .top-content {
        height: auto;
        padding: 16px 28px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .top-breadcrumb {
          color: #757575f0;
          word-break: break-all;
        }
      }

      .middle-content {
        padding: 0 50px;

        .frame1 {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }

        .frame2 {
          margin-block: 8px 24px;
          font-size: 12px;
          color: gray;
        }

        .frame3 {
          display: flex;
          align-items: center;
          gap: 8px;
        }

        .frame4 {
          margin-top: 8px;
          display: flex;
          justify-content: space-between;
          gap: 8px;

          .frame4-i {
            display: flex;
            flex-direction: column;
            gap: 8px;
            font-size: 12px;

            .frame4-i-list-item {
              display: flex;
              gap: 8px;

              .frame4-i-icon {
                width: 24px;
                min-width: 24px;
                height: 24px;
                min-height: 24px;
                border: 1px solid #29d0ff;
                background-color: #29d0ff;
                border-radius: 4px;
                color: white;
              }

              .frame4-i-text-desc {
                display: flex;
                align-items: center;
                flex-wrap: wrap;
              }
            }

            .frame4-i-link-button {
              padding: 0;
              height: auto;
              font-size: 12px;
            }
          }
        }

        .frame-g-c {
          display: flex;
          flex-direction: column;
          gap: 8px;

          .p-title {
            font-size: 15px;
            font-weight: 600;
          }

          .p-content {
            display: flex;
            flex-direction: column;
            gap: 16px;

            .content-row {
              display: flex;
              align-items: center;
              gap: 24px;

              .col-l {
                width: 30%;
                max-width: 280px;
                display: flex;
                justify-content: flex-end;
              }

              .col-r {
                width: 70%;
                display: flex;
                flex-direction: column;
                gap: 4px;
              }
            }
          }
        }

        @media screen and (700px < width) {
          .frame4 {
            flex-direction: row;
          }
        }

        @media screen and (width <= 700px) {
          .frame4 {
            flex-direction: column;
            align-items: center;
          }
        }
      }

      .bottom-content {
        padding: 16px 28px;
        display: flex;
        flex-direction: column;
        gap: 16px;
      }
    }

    .main-content::-webkit-scrollbar {
      width: 8px; /* width of the entire scrollbar */
      height: 8px;
      cursor: pointer;
    }
    .main-content::-webkit-scrollbar-track {
      background: rgba(240 240 240 / 80%); /* color of the tracking area */
      cursor: pointer;
    }
    .main-content::-webkit-scrollbar-thumb {
      background-color: rgba(
        177,
        177,
        177,
        0.4
      ); /* color of the scroll thumb */
      border-radius: 20px; /* roundness of the scroll thumb */
      border: 1px solid rgba(177, 177, 177, 0.4); /* creates padding around scroll thumb */
      cursor: pointer;
    }
  }
}

